header{
  background: rgb(244,244,244);
  background: -moz-linear-gradient(0deg, rgba(244,244,244,1) 0%, rgba(255,255,255,1) 100%);
  background: -webkit-linear-gradient(0deg, rgba(244,244,244,1) 0%, rgba(255,255,255,1) 100%);
  background: linear-gradient(0deg, rgba(244,244,244,1) 0%, rgba(255,255,255,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#f4f4f4",endColorstr="#ffffff",GradientType=1);
  overflow:auto;
  padding: 4px 0px 2px 6px;
  position: fixed;
  width: 100%;
  z-index: 999;
}
header .logo{
  line-height: 1.3;
  margin-left:10px;
  font-size: 1.8em;
  text-indent: 53px;
  display: inline-block;
  position: fixed;
}
header .logo img{
  float:left;
  width:72px;
  margin-left: -68px;
  position: absolute;
}
header nav[role="navigation"]{
  display: inline-block;
  float: right;
  max-height: 55px;
  min-width: 40%;
  line-height:2.7;
}
header nav[role="navigation"] div[role="itemmenu"]{
  display:inline-block;
  min-width:20%;
  text-align: center;
}
header nav[role="navigation"] div[role="itemmenu"] a{
  text-transform: uppercase;
  font-weight: bold;
  font-size: .875rem;
  color: #2d2d2d;
  white-space: nowrap;
  text-decoration: unset;
}
div[role="banner"]{
  width:100%;
  height: auto;
  overflow: auto;
}
div[role="banner"] .banner-imagem-cortada{
  background-image: url('./assets/img/bg1.jpg');
  background-size: cover;
  height: 370px;
  width: 60%;
  display:inline-block;
  float:left;
}
div[role="banner"] .banner-painel{
  background: rgb(244,244,244);
  background: -moz-linear-gradient(0deg, rgba(244,244,244,1) 0%, rgba(255,255,255,1) 100%);
  background: -webkit-linear-gradient(0deg, rgba(244,244,244,1) 0%, rgba(255,255,255,1) 100%);
  background: linear-gradient(0deg, rgba(244,244,244,1) 0%, rgba(255,255,255,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#f4f4f4",endColorstr="#ffffff",GradientType=1);
  float:left;
  width:40%;
  height: 370px;
}
div[role="banner"] .banner-painel-triangulo{
    height: 370px;
    width: 216px;
    transform: skewX(-18deg);
    margin-left: -61px;
    background: rgb(244,244,244);
  background: -moz-linear-gradient(0deg, rgba(244,244,244,1) 0%, rgba(255,255,255,1) 100%);
  background: -webkit-linear-gradient(0deg, rgba(244,244,244,1) 0%, rgba(255,255,255,1) 100%);
  background: linear-gradient(0deg, rgba(244,244,244,1) 0%, rgba(255,255,255,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#f4f4f4",endColorstr="#ffffff",GradientType=1);
  position: absolute;
  z-index: 1;
}
div[role="banner"] .banner-painel-conteudo{
  position: absolute;
  z-index: 100;
  background: rgb(244,244,244);
  background: -moz-linear-gradient(0deg, rgba(244,244,244,1) 0%, rgba(255,255,255,1) 100%);
  background: -webkit-linear-gradient(0deg, rgba(244,244,244,1) 0%, rgba(255,255,255,1) 100%);
  background: linear-gradient(0deg, rgba(244,244,244,1) 0%, rgba(255,255,255,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#f4f4f4",endColorstr="#ffffff",GradientType=1);
}
div[role="main"]{
  background-color:white;
  height: auto;
  overflow: auto;
}
div[role="main"] section{
  min-height: 200px;
  width: 100%;
}
div[role="main"] section article {

}
div[role="main"] section article h3{
  text-align:center;
  font-size:1.4em;
}
.container-atribuicoes{
  display: flex;
}
.container-atribuicoes .conteiner-atribuicoes-item{
  flex:1;
  text-align: center;
  font-size:0.9em;
  color: #2d2d2d;
}
.container-atribuicoes .conteiner-atribuicoes-item img{
  min-height: 190px;
  max-width:190px;
  border-radius:100px;
  display: block;
  cursor:pointer;
  margin-left: auto;
  margin-right: auto;
  background-color: black;
}
.border-vertical-top{
  width: 100%;
  display: block;
  height: 80px;
  transform: skewY(-2deg);
  position: absolute;
  margin-top: -46px;
  background-color: inherit;
}
.section-descricao{
  background-color:#1000ff;
  color:white;
}
.section-descricao-conteudo{
  position:absolute;
  width:100%;
  display:flex;
}
.section-descricao-conteudo-text{
  flex:2;
  text-align:justify;
  text-indent:16px;
  padding-left:32px;
}
.section-descricao-conteudo-image{
  flex:1;
}
.section-descricao-conteudo-image img{
  width: 128px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-radius: 100px;
 
}
.section-mobile{
  background-color:black;
  background-image: url('./assets/img/banner-mobile.png');
  background-size:cover;
  color:#777777;
  height:auto;
  overflow:auto;
}
.section-mobile-text{
  width: 800px;
  display: block;
  margin: 7px auto 7px auto;
  background-color: rgba(37, 37, 37, 0.85);
  padding: 7px 16px;
  border-radius: 5px;
}
